<script setup>
import Button from './Button.vue'
import Popup from './Popup.vue'
import { ref, computed } from 'vue'

const correct = ref([
	{
		text: 'О временном переводе работников на дистанционную работу по инициативе работодателя',
		correctFolder: 'Срок подачи 3 рабочих дня',
		isCorrect: null,
	},
	{
		text: 'О свободных рабочих местах и вакантных должностях',
		correctFolder: 'Срок подачи 5 рабочих дней',
		isCorrect: null,
	},
	{
		text: 'О выполнении квоты для приема на работу инвалидов',
		correctFolder: 'Срок подачи 1 месяц',
		isCorrect: null,
	},
	{
		text: 'ЕФС-1',
		correctFolder: 'Документ не относится к ЦЗН',
		isCorrect: null,
	},
	{
		text: 'О ликвидации и сокращении штата, или численности работников',
		correctFolder: 'Срок подачи 3 рабочих дня',
		isCorrect: null,
	},
	{
		text: 'СТД-Р',
		correctFolder: 'Документ не относится к ЦЗН',
		isCorrect: null,
	},
	{
		text: 'О введении режима неполного рабочего дня или недели',
		correctFolder: 'Срок подачи 3 рабочих дня',
		isCorrect: null,
	},
	{
		text: 'Приказ о переводе на дистанционную работу',
		correctFolder: 'Документ не относится к ЦЗН',
		isCorrect: null,
	},
	{
		text: 'Информация о простое',
		correctFolder: 'Срок подачи 3 рабочих дня',
		isCorrect: null,
	},
	{
		text: 'Информация о банкротстве организации',
		correctFolder: 'Срок подачи 3 рабочих дня',
		isCorrect: null,
	},
])

const draggedCard = ref(null)

function onDragStart(item) {
	draggedCard.value = item // Запоминаем, какая карточка была взята
}

function onDragOver(event) {
	event.preventDefault() // Разрешить перетаскивание
}

function onDrop(event, folderName) {
	event.preventDefault()
	event.target.classList.remove('tadam')
	event.target.classList.add('tadam')
	window.setTimeout(() => {
		event.target.classList.remove('tadam')
	}, 1000)

	if (draggedCard.value.correctFolder === folderName) {
		draggedCard.value.isCorrect = true
		goodCount.value += 1
	} else {
		draggedCard.value.isCorrect = false
		failCount.value += 1
	}
	correct.value = correct.value.filter(card => card !== draggedCard.value)

	draggedCard.value = null // Сбрасываем текущую перетаскиваемую карточку
}

const failCount = ref(0)

const goodCount = ref(0)

const isShowPopup = ref(false)
</script>

<template>
	<div v-if="isShowPopup" class="container__popup">
		<Popup :failCount="failCount"></Popup>
	</div>
	<div class="container">
		<div class="container__folder">
			<img
				class="img1"
				src="../assets/голубая папка-Photoroom.png"
				alt="Срок подачи 3 рабочих дня"
				@dragover="onDragOver"
				@drop="event => onDrop(event, 'Срок подачи 3 рабочих дня')"
			/>

			<img
				class="img2"
				src="../assets/желтая папка-Photoroom.png"
				alt="Срок подачи 5 рабочих дней"
				@dragover="onDragOver"
				@drop="event => onDrop(event, 'Срок подачи 5 рабочих дней')"
			/>
			<div class="folder-name">Срок подачи 3 рабочих дня</div>
			<div class="folder-name">Срок подачи 5 рабочих дней</div>
			<img
				class="img3"
				src="../assets/зеленая папка-Photoroom.png"
				alt="Документ не относится к ЦЗН"
				@dragover="onDragOver"
				@drop="event => onDrop(event, 'Документ не относится к ЦЗН')"
			/>
			<img
				class="img4"
				src="../assets/синяя папка-Photoroom.png"
				alt="Срок подачи 1 месяц"
				@dragover="onDragOver"
				@drop="event => onDrop(event, 'Срок подачи 1 месяц')"
			/>
			<div class="folder-name">Документ не относится к ЦЗН</div>
			<div class="folder-name">Срок подачи 1 месяц</div>
		</div>

		<div class="container__card">
			<div
				v-for="item in correct"
				:key="item.text"
				class="card"
				:class="{
					correct: item.isCorrect === true,
					incorrect: item.isCorrect === false,
				}"
				draggable="true"
				@dragstart="() => onDragStart(item)"
			>
				{{ item.text }}
			</div>
		</div>

		<div v-if="correct.length === 0 && !isShowPopup" class="container__btn">
			<Button @click="isShowPopup = true">Проверить</Button>
		</div>
	</div>
</template>

<style scoped>
.container {
	margin: 0 1.5vw;
}

.container__folder {
	display: grid;
	grid-template-columns: 1fr 1fr;
	justify-content: center;
	align-items: center;
	gap: 5vw;
	margin-bottom: 5vw;
}

.container__folder img {
	margin: 0 auto;
}

.container__folder .img3 {
	width: 20vw;
	height: auto;
}

.container__folder .img1 {
	width: 32vw;
}

.container__folder .img2 {
	width: 40vw;
}

.container__folder .img4 {
	width: 20vw;
}

.container__card {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 2vw;
}

.card {
	border: 2px solid #43739e;
	padding: 2vw 1vw;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #d7dde4;
	cursor: grab;
	transition: background-color 0.3s, border-color 0.3s;
	font-size: 0.85em;
}

.card.correct {
	background-color: #d4edda;
	border-color: #28a745;
}

.card.incorrect {
	background-color: #f8d7da;
	border-color: #dc3545;
}

.card:nth-child(10) {
	grid-column: 2/2;
}

.container__btn {
	display: flex;
	justify-content: center;
	align-items: center;
	animation: fade alternate 1s;
}

.container__popup {
	position: absolute;
	background-color: rgba(128, 128, 128, 0.585);
	z-index: 10;
	width: 100vw;
	height: 100vh;
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
	align-items: center;
	animation: fade alternate 1s;
}

@keyframes fade {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

.folder-name {
	font-size: 0.8em;
	text-align: center;
	margin-top: -7.5vw;
}

.tadam {
	animation: tadam 1s alternate;
}

@keyframes tadam {
	from {
		scale: 1;
	}
	50% {
		scale: 1.1;
	}
	to {
		scale: 1;
	}
}
</style>
